.account {
  --v-account-accounts-padding-y: 15px;
  --v-account-accounts-padding-x: 20px;
  --v-account-content-gap: 2.6667rem;
  --v-account-content-item-gap: 2rem;
  --v-account-subtitle-group-gap: 1.3333rem;
  --v-account-subtitle-btns-gap: 0.6667rem;
}
.account__content {
  display: flex;
  flex-direction: column;
  gap: var(--v-account-content-gap);
}
.account__subtitle {
  line-height: 1;
  display: flex;
  align-items: center;
}
.account__subtitle-group {
  display: flex;
  flex-grow: 1;
  gap: var(--v-account-subtitle-group-gap);
}
.account__subtitle-btns {
  display: flex;
  gap: var(--v-account-subtitle-btns-gap);
}
.account__item {
  display: flex;
  flex-direction: column;
  gap: var(--v-account-content-item-gap);
}

.my-accounts {
  --v-my-accounts-item-gap: 1rem;
}
.my-accounts__row {
  --v-gutter-y: 2rem;
}
.my-accounts__item {
  display: flex;
  flex-direction: column;
  gap: var(--v-my-accounts-item-gap);
  padding: var(--v-account-accounts-padding-y) var(--v-account-accounts-padding-x);
  background-color: var(--v-black);
}
.my-accounts__price {
  font-size: 3.3333rem;
  font-weight: 500;
  text-align: end;
  line-height: 1;
}

.last-order {
  --v-last-order-row-padding-x: 1.3333rem;
  --v-last-order-row-padding-y: 1rem;
  --v-last-order-row-gap: 2rem;
  --v-last-order-row-border-bottom-color: var(--v-gray-700);
  --v-last-order-text-color: var(--v-white);
}
.last-order__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: var(--v-black);
}
.last-order__row {
  padding: var(--v-last-order-row-padding-y) var(--v-last-order-row-padding-x) var(--v-last-order-row-padding-y) var(--v-last-order-row-padding-x);
  border-bottom: 1px solid var(--v-last-order-row-border-bottom-color);
  justify-content: space-between;
}
.last-order__row:first-child {
  --v-last-order-row-border-bottom-color: var(--v-yellow);
  --v-last-order-row-padding-y: 0.6667rem;
  --v-last-order-text-color: var(--v-gray-500);
  color: var(--v-gray-500) !important;
}
.last-order__row:last-child {
  border-bottom: none;
}
.last-order__col:last-child {
  justify-content: end;
}
.last-order__col:last-child .last-order__text {
  font-size: 1rem;
  font-weight: 500;
}
.last-order__text {
  color: var(--v-acc-table-text-color);
}

.status {
  --v-status-gap: 0.6667rem;
  display: flex;
  align-items: center;
  gap: var(--v-status-gap);
}
.status--processing i {
  color: var(--v-orange);
}
.status--shipped i {
  color: var(--v-yellow);
}
.status--cancelled i {
  color: var(--v-danger);
}
.status--delivered i {
  color: var(--v-lime);
}
.status--completed i {
  color: var(--v-secondary);
}

.profile-data {
  --v-profile-data-gap: 2rem;
  --v-profile-data-item-gap: 0.6667rem;
  display: flex;
  flex-direction: column;
  gap: var(--v-profile-data-gap);
}
.profile-data__row {
  --v-gutter-y: 2rem;
}
.profile-data__item {
  --v-profile-data-item-padding: 1.3333rem;
  display: flex;
  flex-direction: column;
  gap: var(--v-profile-data-item-gap);
  padding: var(--v-profile-data-item-padding);
  background-color: var(--v-black);
}

.product-order {
  --v-product-order-row-padding-x: 1.3333rem;
  --v-product-order-row-padding-y: 0.6667rem;
  --v-product-order-row-gap: 2rem;
  --v-product-order-row-border-bottom-color: var(--v-gray-700);
}
.product-order__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: var(--v-black);
}
.product-order__row {
  padding: var(--v-product-order-row-padding-y) var(--v-product-order-row-padding-x) var(--v-product-order-row-padding-y) var(--v-product-order-row-padding-x);
  border-bottom: 1px solid var(--v-product-order-row-border-bottom-color);
  justify-content: space-between;
}
.product-order__row:first-child {
  --v-product-order-row-border-bottom-color: var(--v-yellow);
  color: var(--v-secondary);
}
.product-order__row--card {
  --v-product-order-row-padding-x: 0;
  --v-product-order-row-padding-y: 0;
}
.product-order__row--description {
  --v-product-order-row-padding-y: 1rem;
  --v-product-order-row-border-bottom-color: var(--v-yellow);
}
.product-order__col {
  text-align: end;
}
.product-order__price--final {
  color: var(--v-orange);
}
.product-order__total {
  line-height: 1;
  padding: 20px;
}

.order-card {
  --v-order-card-content-padding: 1.3333rem;
  --v-order-card-img-size: 100%;
  display: flex;
  flex-grow: 1;
}
.order-card__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--v-order-card-content-padding);
  width: 100%;
}
.order-card__img {
  position: relative;
  max-width: 170px;
  width: 100%;
  aspect-ratio: 1;
}
.order-card__img * {
  width: var(--v-order-card-img-size);
  height: var(--v-order-card-img-size);
  object-fit: contain;
}
.order-card__left {
  display: flex;
  height: 100%;
}
.order-card__col:nth-child(n+2) {
  text-align: end;
}
.order-card__group {
  --v-order-card-group-padding: 1.3333rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--v-order-card-group-padding);
  height: 100%;
}
.order-card__count {
  --v-order-card-count-padding: 1.3333rem;
  color: var(--v-secondary);
  padding-top: var(--v-order-card-count-padding);
  font-weight: bold;
}

.order-tracking {
  --v-order-tracking-row-padding-y: 1rem;
  --v-order-tracking-row-padding-x: 1.3333rem;
  --v-order-tracking-row-border-bottom-color: var(--v-gray-700);
}
.order-tracking__container {
  background-color: var(--v-black);
}
.order-tracking__row {
  padding: var(--v-order-tracking-row-padding-y) var(--v-order-tracking-row-padding-x) var(--v-order-tracking-row-padding-y) var(--v-order-tracking-row-padding-x);
  border-bottom: 1px solid var(--v-order-tracking-row-border-bottom-color);
}
.order-tracking__row:last-child {
  border-bottom: none;
}
.order-tracking__row--steps {
  --v-order-tracking-row-padding-y: 0.6667rem;
  --v-order-tracking-row-padding-x: 1.3333rem;
  padding: var(--v-order-tracking-row-padding-y) var(--v-order-tracking-row-padding-x) 0 var(--v-order-tracking-row-padding-x);
  border-bottom: none;
}
.order-tracking__row--steps .row {
  --v-gutter-x: 0.6667rem;
}
.order-tracking__row--header {
  --v-order-tracking-row-padding-y: 1.3333rem;
  --v-order-tracking-row-border-bottom-color: var(--v-yellow);
  color: var(--v-secondary);
}
.order-tracking__col:first-child .order-tracking__text {
  color: var(--v-gray-500);
}
.order-tracking__text {
  color: var(--v-secondary);
}
.order-tracking__step {
  --v-acc-col-step-border-bottom-color: var(--v-orange);
  height: 100%;
  border-bottom: 5px solid var(--v-acc-col-step-border-bottom-color);
}
.order-tracking__step--sent {
  --v-acc-col-step-border-bottom-color: var(--v-yellow);
}
.order-tracking__step--arrived {
  --v-acc-col-step-border-bottom-color: var(--v-secondary);
}
.order-tracking__step-text {
  margin-bottom: 0.6667rem;
}